@import '~scss/variables';
@import '~scss/custom-properties';

.ht-import {
  height: 500px;
  max-height: 70vh;
  overflow: hidden;

  // Prevent clipping tooltip in body
  .ht_master td, .ht_master th, .ht_clone_top th {
    position: relative;
    overflow: visible;
  }

  // Invalid handsontable cell
  .ht-invalid {

    &::after {
      content: '';
      position: absolute;
      top: -1px;
      left: -1px;
      right: -1px;
      bottom: -1px;
      margin: auto;
      border: 1px solid var(--danger-color);
    }

    // Invalid value marker
    .fa {
      position: absolute;
      right: 2px;
      top: 0;
      line-height: 22px;
      color: var(--danger-color);
    }
  }

  // Reduce selects size
  .ff-select.ff-select {
    width: auto;
    padding: 2px 18px 2px 2px;
    border-bottom: 0;
    min-height: 1.6rem;
    min-width: 100%;
    max-width: 160px;

    &.ff-select.ng-invalid { // invalid select in column header
      color: $color-danger;
      box-shadow: inset 0 -2px $color-danger;
    }
  }


  // Ignored rows
  .ht-ignore {
    opacity: 0.5;
  }

  // Row with headers (automatically detected)
  .ht-head-row {
    background-color: var(--text-color);
    color: #fff;
    opacity: 1;
  }
}
